html,
body,
#root {
  height: 100%;
  width: 100%;
}

body {
  font-family: system-ui;
  margin: 0;
}

*,
*:after,
*:before {
  box-sizing: border-box;
}

.bg-video {
  background-size: cover;
  background: no-repeat center;
  user-select: none;
  margin-bottom: 20px;
  z-index: 1;
  /*custom styles, for example grayscale appearance*/
  filter: saturate(120%);
}

h1 {
    margin-left: 5px;
    margin-right: 5px; 
    font-size: 40px;
    font-family: "Honk", system-ui;
    font-optical-sizing: auto;
    font-weight: 400;
    font-style: normal;
    font-variation-settings:
      "MORF" 15,
      "SHLN" 50;
}

p {
  font-size: 25px;
  font-family: "Lato", sans-serif;
  font-weight: 100;
  font-style: italic;
}